import uniIcon from "@/components/uni-icon/uni-icon.vue"

export default {
	 components: {uniIcon},
	data() {
		return {
			searchKey: [],
			ipt: '',
			searchrs:{},
			searchClose: true,

		}
	},
	onShow() {
		var vv = uni.getStorageSync('searchLocal');
		var arr = vv.split("-");
		this.searchKey = arr;
		
	},
	methods: {

		clearKey: function() {
			var that = this;
			uni.showModal({
				title: '提示',
				content: '点击确定将删除所有历史信息，确定删除吗？',
				success: function(res) {
					if (res.confirm) {
						that.searchKey = [];
						uni.setStorage({
							key: 'searchLocal',
							data: that.searchKey
						});
						
					} else if (res.cancel) {

					}å
				}
			});

		},
		searchNow: function(e) {
			if (this.ipt == '') {
				uni.showToast({
					title: '未输入搜索关键字',
					icon: 'none',
					duration: 1000
				});
				return false;
			}
			var that = this;
			that.searchrs={};
			var newArr = that.searchKey;
			newArr.push(this.ipt);
			this.searchKey = newArr;
			this.callsb(this.ipt);
			var newStr = newArr.join('-');
			uni.setStorage({
				key: 'searchLocal',
				data: newStr
			});
		},
		callsb:function(val){
			console.log("**********call扇贝*******"+val);
			
			const requestUrl = 'https://api.shanbay.com/bdc/search/?word='+val
			var that = this;
			uni.request({
				url: requestUrl,
				dataType: 'text',
				data: {
					noncestr: Date.now()
				},
				success: (res) => {
				console.log(res);
				let datajson=JSON.parse(res.data);
				if("SUCCESS"==datajson.msg){
					// string 转 json
					that.searchrs=datajson.data;
					console.log(that.searchrs);
					uni.setStorage({
						key: 'searchrsLocal',
						data: that.searchrs
					});
				}
					
					
				},
				fail: (err) => {
					console.log('request fail', err);
					uni.showModal({
						content: err.errMsg,
						showCancel: false
					});
				},
				complete: () => {
					this.loading = false;
				}
			});
		},
		callyoudao:function(val){
			console.log("**********callyoudao*******"+val);
			const duration = 2000;
			const requestUrl = 'http://fanyi.youdao.com/translate?&doctype=json&type=AUTO&i='+val
			
			uni.request({
				url: requestUrl,
				dataType: 'text',
				data: {
					noncestr: Date.now()
				},
				success: (res) => {
					console.log('request success', res)
					console.log("***************1*******")
					console.log(res)
					console.log("***************2*******")
					console.log(res.data)
						console.log("***************3*******")
					let translateResult=JSON.parse(res.data);
					console.log(translateResult)
					console.log("***************4*******")
					
					console.log(translateResult.translateResult[0][0].tgt)
					this.res = '请求结果 : ' + JSON.stringify(res);
					// let  srcval=this.res.data.translateResult[0].src;
					// let   tgtval=this.res.data.translateResult[0].tgt;
					console.log(this.res);
					console.log(this.res.data);
				},
				fail: (err) => {
					console.log('request fail', err);
					uni.showModal({
						content: err.errMsg,
						showCancel: false
					});
				},
				complete: () => {
					this.loading = false;
				}
			});
		},
		addtobook:function(){
			console.log("***********addtobook *******");
			var userinfo = uni.getStorageSync('user');
			let rsdata= uni.getStorageSync('searchrsLocal');
			 // let username=userinfo.nickName;
			 
			 console.log("***********addtobook ***userinfo****",userinfo);
			 console.log("***********addtobook ***rsdata****",rsdata);
			 
			 const requestUrl = 'http://localhost:9001/core/book/add'
			 var that = this;
			 let requestdata={};
			 requestdata.openid=userinfo.openid;
			 requestdata.content=rsdata.content;
			 requestdata.pron=rsdata.pron;
			 requestdata.definition=rsdata.definition;
			 requestdata.audio_addresses=rsdata.audio_addresses.us[0];
			
			 var datarequest = JSON.stringify(requestdata);
			  console.log("**datarequest****",datarequest);
			 uni.request({
			 	url: requestUrl,
			 	dataType:'json',
			 	data: datarequest,
				method:'POST',
			 	success: (res) => {
			 	console.log(res);
			 
			 		
			 		
			 	},
			 	fail: (err) => {
			 		console.log('request fail', err);
			 		uni.showModal({
			 			content: err.errMsg,
			 			showCancel: false
			 		});
			 	},
			 	complete: () => {
			 		this.loading = false;
			 	}
			 });
			 
		}
	}
}
